.banner-container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 892px;
}

.banner-container .navbar-container {
  height: 64px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 80px;
  color: white;
}
.banner-container .navbar-container .navbar-left {
  font-size: 24px;
}

.banner-container #banner-bg {
  position: absolute;
  z-index: -5;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgb(39, 48, 54);
  background-image: url(../../images/dots-grey.svg);
  background-repeat: repeat;
  height: 100%;
}

.banner-container .intro-content {
  position: relative;
  font-size: 18px;
  color: whitesmoke;
  font-family: "Comfortaa", sans-serif;
  padding: 80px;
  line-height: 1.5;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.banner-container .intro-content .left {
  width: 50%;
}
.banner-container .intro-content .left .title {
  position: relative;
  font-size: 50px;
  color: #00cba9;
  font-weight: bold;
}

.banner-container .intro-content .left .title span.emoji-wave {
  animation-name: wave-animation;
  animation-duration: 2.5s;
  animation-iteration-count: infinite;
  transform-origin: 70% 70%;
  display: inline-block;
}

@keyframes wave-animation {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-10deg);
  }
  20% {
    transform: rotate(12deg);
  }
  30% {
    transform: rotate(-10deg);
  }
  40% {
    transform: rotate(9deg);
  }
  50% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.banner-container .intro-content .right {
  width: 40%;
  display: flex;
  align-items: center;
}

.banner-container .intro-content .right .video {
  position: absolute;
  z-index: 1;
}

.banner-container .intro-content .right .diagonals {
  position: relative;
  z-index: -3;
  height: 360px;
  width: 500px;
  display: -webkit-box;
  display: -moz-box;
  display: box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: stretch;
  -moz-box-align: stretch;
  box-align: stretch;
  -webkit-align-items: stretch;
  -moz-align-items: stretch;
  -ms-align-items: stretch;
  -o-align-items: stretch;
  align-items: stretch;
  -ms-flex-align: stretch;
}

.banner-container .intro-content .right .diagonals .diagonal {
  height: 100%;
  background: #f45d4c;
  -webkit-box-flex: 1;
  -moz-box-flex: 1;
  box-flex: 1;
  -webkit-flex: 1;
  -moz-flex: 1;
  -ms-flex: 1;
  flex: 1;
  -webkit-transform: rotate(45deg) translateY(10%) translateX(30%);
  -moz-transform: rotate(45deg) translateY(10%) translateX(30%);
  -ms-transform: rotate(45deg) translateY(10%) translateX(30%);
  -o-transform: rotate(45deg) translateY(10%) translateX(30%);
  transform: rotate(45deg) translateY(10%) translateX(30%);
}
.banner-container .intro-content .right .diagonals .diagonal:nth-child(2) {
  background: #f7a541;
}
.banner-container .intro-content .right .diagonals .diagonal:nth-child(3) {
  background: #faca66;
}
.banner-container .intro-content .right .diagonals .diagonal:nth-child(4) {
  background: #60b99a;
}
.banner-container .intro-content .right .diagonals .diagonal:nth-child(5) {
  background: #54787d;
}
.banner-container .intro-content .right .diagonals .diagonal:nth-child(6) {
  background: #5b527f;
}
